.skillsContainer {
    display: flex;
    align-items: center;
    justify-content: center; 
    width: 100%;
    margin-top: 3.5rem;
    padding: 0 2rem;
    flex-direction: column;
}

.skill--scroll-1,.skill--scroll-2 {
    display: flex;
    width: 100%;
    margin: 0 2rem;
}
.skill--scroll-2 {
    margin-top: 2rem;
  }

.skill--box {
    box-shadow: 8px 8px 12px var(--shadow-primary);
    backdrop-filter: blur(4px);
    width: 160px;
    height: 160px;
    margin: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 1rem;
    transition: 300ms ease-in-out;

    
}
#s1{
    border-radius: 10px 10px 10px 10px;
    background-color: bisque;
}
#s2{
    border-radius: 10px 10px 10px 10px;
}

.skill-name {
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1rem;
    color: var(--color-primary);
    
}

.skill--box:hover  {
    transform: scale(1.15);
}
.skill--box > img {
    height: 70px;
    width: auto;
    pointer-events: none;
}

.marquee {
    padding: 3rem 0;
}

@media screen and (max-width: 1024px){
    .skillsContainer {
        padding: 1rem;
        margin: 1rem 0;
    }
    .skill--box {
        width: 150px;
        height: 150px;
        margin: 1.2rem;
        padding: 2rem 1rem;
    }

    .skill--box > img {
        height: 45px;
    }
}

@media screen and (max-width: 900px) {
    .skillsContainer {
        padding: 0.5rem;
    }
    .skill--scroll {
        width: 100%;
        margin: 0;
    }
}
  
@media screen and (max-width: 600px){
    .skill--box {
        width: 135px;
        height: 135px;
        margin: 1.2rem;
        padding: 2rem 1rem;
    }

    .skill--box > img {
        height: 40px;
    }

    .skills__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}